<template>
  <div>
    <div class="ui container">
      <Barra></Barra>

      <h1>Proximamente</h1>
      <div
        class="ui segment divided items cont"
        :class="loading ? 'loading' : ''"
      >
        <ItemPelicula
          v-for="(item, index) in peliculas"
          @click.native="toPelicula(item)"
          :key="index"
          :item="item"
        >
        </ItemPelicula>
      </div>
    </div>

    <modaladd :callbackPelicula="loadPeliculas"></modaladd>
    <modalchange
      :item="itemSelected"
      :callbackClose="toglemodalchage"
      :callbackRefresh="loadPeliculas"
      v-if="modalchage"
    ></modalchange>
  </div>
</template>

<script>
// components
import Barra from "../components/Barra";
import ItemPelicula from "../components/ItemPelicula";
import modaladd from "../components/Proximamente/modaladd";
import modalchange from "../components/Proximamente/modalechange";

import axios from "../services/axios";

export default {
  name: "proximamente",
  methods: {
    loadPeliculas() {
      this.loading = true;
      axios.get("/api/proximamente").then((res) => {
        this.peliculas = res.data;
        this.loading = false;
      });
    },
    toPelicula(item) {
      this.itemSelected = item;
      this.toglemodalchage();
    },
    toglemodalchage() {
      this.modalchage = !this.modalchage;
    },
  },
  data: () => {
    return {
      peliculas: [],
      loading: true,
      modalchage: false,
      itemSelected: {},
    };
  },
  created() {
    this.$store.commit("chageIndex", {
      newIndex: 1,
    });
    this.loadPeliculas();
  },
  components: {
    Barra,
    modaladd,
    modalchange,
    ItemPelicula,
  },
};
</script>

