<template>
  <div>
    <div class="ui ico button green add" @click="togglemodaladd()">
      <i class="plus icon"></i>
    </div>
    <div class="fondo" v-if="showadd">
      <div class="ui container">
        <div
          class="ui segment animate__animated animate__fadeInDown"
          :class="postnewdata ? 'loading' : ''"
        >
          <div class="ui form">
            <div class="field">
              <div class="ui header">Post new movie</div>
            </div>
            <div class="field">
              <label>Title</label>
              <input type="text" v-model="newdata.title" />
            </div>
            <div class="field">
              <label>Imagen</label>
              <input type="file" accept="image/*" @change="onFileChange" />
            </div>
            <div class="field">
              <label>Description</label>
              <textarea v-model="newdata.description"></textarea>
            </div>
            <input
              class="ui blue button"
              type="button"
              value="Enviar"
              @click="sendnewdata()"
            />
            <input
              class="ui button cancel red"
              type="button"
              value="Cancelar"
              @click="togglemodaladd()"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../../services/axios";

export default {
  methods: {
    togglemodaladd() {
      this.showadd = !this.showadd;
    },
    sendnewdata() {
      if (
        this.newdata.title &&
        this.newdata.description &&
        this.newdata.image
      ) {
        let form = new FormData();
        form.append("title", this.newdata.title);
        form.append("description", this.newdata.description);
        form.append("portada", this.newdata.image);
        this.postnewdata = true;

        axios.post("/api/proximamente", form).then((res) => {
          console.log(res);
          if (res.data["status"] == 200) {
            this.postnewdata = false;
            this.callbackPelicula();

            this.togglemodaladd();
          }
        });
      }
    },
    onFileChange(e) {
      this.newdata.image = e.target.files[0];
    },
  },
  data: () => {
    return {
      showadd: false,
      postnewdata: false,
      newdata: {
        title: null,
        image: null,
        description: null,
      },
    };
  },
  props: {
    callbackPelicula: Function,
  },
};
</script>

<style lang="scss">
.button.add {
  position: absolute;
  bottom: 5px;
  right: 0;
}
</style>