<template>
  <div class="fondo">
    <div class="ui container">
      <div
        class="ui segment padded animate__animated animate__fadeInDown"
        :class="requestate ? 'loading' : ''"
      >
        <div class="ui form">
          <div class="field">
            <label>Title</label>
            <input type="text" v-model="updateData.title" />
          </div>
          <div class="field">
            <label>Imagen</label>
            <input type="file" accept="image/*" @change="onFileChange" />
          </div>
          <div class="field">
            <img
              class="ui tiny image centered rounded"
              :src="updateData.portada"
            />
          </div>
          <div class="field">
            <label>Description</label>
            <textarea v-model="updateData.description"></textarea>
          </div>

          <div class="field">
            <button class="ui icon button red" @click="this.delete">
              <i class="trash icon"></i>
            </button>
            <button class="ui icon button green" @click="update">
              <i class="save icon"></i>
            </button>
            <button class="ui icon button right floated" @click="callbackClose">
              <i class="cancel icon"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../../services/axios";

export default {
  name: "modalchange",
  methods: {
    onFileChange(e) {
      this.updateData.portada = URL.createObjectURL(e.target.files[0]);
      this.updateData.image = e.target.files[0];
    },
    update() {
      let form = new FormData();
      form.append("title", this.updateData.title);
      form.append("description", this.updateData.description);
      form.append("id", this.updateData.id);
      // form.append("estreno", this.updateData.estreno);

      if (this.updateData.image != null)
        form.append("portada", this.updateData.image);

      this.requestate = true;
      axios.put("/api/proximamente", form).then((res) => {
        this.requestate = false;
        if (res.data["status"] == 200) {
          this.callbackRefresh();
          this.callbackClose();
        }
      });
    },
    delete() {
      if (confirm("Desea eliminar? ")) {
        this.requestate = true;
        axios.delete("/api/proximamente/" + this.item["id"]).then((res) => {
          this.requestate = false;
          this.callbackRefresh();
          this.callbackClose();
        });
      }
    },
  },
  props: {
    item: Object,
    callbackClose: Function,
    callbackRefresh: Function,
  },
  data: () => {
    return {
      updateData: {
        title: String,
        description: String,
        portada: String,
        id: String,
        image: null,
      },
      requestate: false,
    };
  },
  created() {
    this.updateData.title = this.item["title"];
    this.updateData.description = this.item["description"];
    this.updateData.portada = this.item["portada"];
    this.updateData.id = this.item["id"];
  },
};
</script>

<style lang="scss">
.fondo {
  position: absolute;
  top: 0;
  background: rgba(0.5, 0.5, 0.5, 0.5);
  width: 100vw;
  height: 100vh;
}
</style>